.container {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   width: 320px;
   height: 320px;
   display: flex;
   flex-wrap: wrap;
   user-select: none;
   font-family: Arial, Helvetica, sans-serif;
   background-color: rgb(18, 204, 218);
   padding: 10px;
   border-radius: 15px;
   box-shadow: 1px 1px 4px 1px #000000ab;
}

.box {
   width: 100px;
   height: 100px;
   border: 2px solid #00000073;
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: 70px;
}

.box1,
.box2,
.box3 {
   border-top: none;
}
.box3,
.box6,
.box9 {
   border-right: none;
}

.box1,
.box4,
.box7 {
   border-left: none;
}
.box7,
.box8,
.box9 {
   border-bottom: none;
}

.boxColor {
   color: #f63a68;
}
