.DevContainer {
   display: flex;
   flex-direction: column;
   justify-content: center;
   padding-left: 20px;
   padding-right: 20px;
   position: relative;
   border-radius: 10px;
   height: 100%;
   box-shadow: 1px 1px 4px 1px #000000ab;
   overflow: hidden;
}
.DevContainer::after {
   content: '';
   background: url(./../images/avater.jpg);
   background-color: #000000;
   background-size: cover;
   background-repeat: no-repeat;
   background-position-x: right;
   background-position-y: center;
   position: absolute;
   width: 100%;
   height: 100%;
   top: 0;
   left: 0;
   border-radius: 10px;
   transition: all 0.5s;
}
.DevParent:hover .DevContainer::after,
.DevParent:focus .DevContainer::after {
   transform: scale(1.1);
}
.DevContainer h2 {
   color: #ffffff;
   font-size: 30px;
   margin-bottom: 10px;
   z-index: 1;
}

.DevContainer p {
   color: #ffffff;
   margin-bottom: 10px;
   z-index: 1;
}

.DevContainer a {
   color: #ffffff;
   text-decoration: none;
   background-color: #000000a4;
   width: 100px;
   text-align: center;
   padding: 5px;
   border-radius: 5px;
   text-transform: uppercase;
   margin-top: 10px;
   z-index: 1;
}

.DevParent {
   position: absolute;
   width: 100%;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   z-index: 10;
   transition: all 1s;
   width: 90%;
   height: 60%;
}

.DevContainer span {
   background-color: rgba(0, 0, 0, 0.271);
   font-weight: 600;
   border-top-right-radius: 10px;
   border-bottom-left-radius: 10px;
   width: 50px;
   position: absolute;
   top: 0;
   right: 0;
   display: flex;
   justify-content: center;
   align-items: center;
   cursor: pointer;
   z-index: 10;
}

.DevContainer span:hover {
   background-color: crimson;
   color: #fff;
}
