.donateContainer{
    bottom: 15%;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 10px;
    position: absolute;
    backdrop-filter: blur(2px);
    background-color: rgba(18, 205, 218, 0.348);
    width: 90%;
    height: 100px;
    z-index: 10;
    box-shadow: 1px 1px 4px 1px #000000ab;
}
.donateContainer div{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.donateContainer div span{
    background-color: rgba(0, 0, 0, 0.271);
    font-weight: 600;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    width: 50px;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.donateContainer div h2{
    color: rgb(255, 255, 255);
    background-color: rgba(0, 0, 0, 0.44);
    padding: 3px 15px;
    border-radius: 10px;
}

.donateContainer div span:hover{
    background-color: crimson;
    color: #fff;
}