.footerContainer {
   position: sticky;
   top: 100%;
   background-color: rgb(18, 204, 218);
   padding: 20px 0px;
   box-shadow: 1px 1px 4px 1px #00000085;
}

.footerContainer ul li {
   background-color: rgb(41, 230, 243);
   border-radius: 5px;
   padding: 5px 10px;
   box-shadow: 1px 1px 1px 1px #0000001a;
   cursor: pointer;
   transition: 0.5s;
   font-weight: 600;
   text-transform: uppercase;
}
.footerContainer ul {
   display: flex;
   list-style: none;
   justify-content: space-evenly;
}

.footerContainer ul li a {
   text-decoration: none;
   color: inherit;
}

.footerContainer ul li:hover {
   box-shadow: inset 1px 1px 1px 1px #0000004d;
}
