.logo h1 {
   padding-top: 10px;
   text-align: center;
   color: #053f50;
}

.navContainer {
   position: sticky;
   top: 0px;
   background-color: rgb(18, 204, 218);
   box-shadow: 1px 1px 4px 1px #0000006a;
}

.link ul {
   display: flex;
   list-style: none;
   justify-content: space-evenly;
}

.link {
   background-color: rgb(18, 204, 218);
   padding: 10px 0px 15px 0px;
   font-weight: 600;
   text-transform: uppercase;
}

.link ul li {
   background-color: rgb(41, 230, 243);
   border-radius: 5px;
   padding: 5px 10px;
   box-shadow: 1px 1px 1px 1px #0000001a;
   cursor: pointer;
   transition: 0.5s;
}

.link ul li:hover {
   box-shadow:inset 1px 1px 1px 1px #0000004d;
}
