.homeContainer {
   background-color: rgb(18, 204, 218);
   width: 90%;
   border-radius: 10px;
   padding: 10px;
   display: flex;
   flex-direction: column;
   align-items: center;
   position: absolute;
   z-index: 1;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   box-shadow: 1px 1px 4px 1px #000000ab;
}

.homeContainer span {
   background-color: rgb(41, 230, 243);
   border-radius: 5px;
   padding: 5px 10px;
   box-shadow: 1px 1px 1px 1px #0000001a;
   cursor: pointer;
   transition: 0.5s;
   text-align: center;
}

.homeContainer div div {
   display: flex;
   justify-content: space-evenly;
   width: 100%;
   margin: 5px;
   transition: all 0.5s;
}
.homeContainer p,
.homeContainer h2,
.homeContainer h3 {
   margin: 5px;
}


.homeContainer span:hover {
   box-shadow: inset 1px 1px 1px 1px #0000004d;
}

.homeContainer span {
   width: 100px;
}
.homeHeading {
   position: relative;
}
.confirm {
   position: absolute;
   top: 0;
   right: 0;
   border-radius: 5px;
   padding: 5px 10px;
   cursor: pointer;
}
